import React from 'react';
import { graphql, Link, PageProps } from 'gatsby';

import Layout from '../../components/layout';
import SEO from '../../components/seo';

import photo from '../../images/placeholder/photo-1547586696-ea22b4d4235d.webp';

type Props = PageProps<GatsbyTypes.ProductPagesQuery>;

const ProductsIndex: React.FC<Props> = ({ data }) => {
  const { nodes: products } = data.allMarkdownRemark;
  return (
    <Layout>
      <SEO title="Products" />

      <div className="pt-16 pb-20 px-4 sm:px-6 lg:pt-24 lg:pb-28 lg:px-8">
        <div className="relative max-w-7xl mx-auto">
          <h2 className="text-3xl leading-9 tracking-tight font-extrabold text-gray-900 sm:text-4xl sm:leading-10">
            Products
          </h2>

          <div className="mt-6 grid gap-5 max-w-lg mx-auto border-t-2 border-gray-100 pt-10 lg:grid-cols-3 lg:max-w-none">
            {products.map((node) => {
              const title = node.frontmatter?.title || node.fields?.slug;

              return (
                <article
                  key={node.id}
                  className="flex flex-col rounded-lg shadow-lg overflow-hidden"
                >
                  <div className="flex-shrink-0">
                    <img className="h-48 w-full object-cover" src={photo} alt={title} />
                  </div>
                  <div className="flex-1 bg-white p-6 flex flex-col justify-between">
                    <div className="flex-1">
                      <Link className="block" to={node.fields?.slug || ''}>
                        <header>
                          <h3 className="mt-2 text-xl leading-7 font-semibold text-gray-900">
                            {title}
                          </h3>
                        </header>
                        <section>
                          {/* eslint-disable react/no-danger */}
                          <p
                            className="mt-3 text-base leading-6 text-gray-500"
                            dangerouslySetInnerHTML={{
                              __html: node.frontmatter?.description || node.excerpt || '',
                            }}
                          />
                          {/* eslint-enable react/no-danger */}
                        </section>
                      </Link>
                    </div>
                  </div>
                </article>
              );
            })}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default ProductsIndex;

export const pageQuery = graphql`
  query ProductPages {
    allMarkdownRemark(filter: { fields: { slug: { glob: "/products/*" } } }) {
      nodes {
        excerpt(pruneLength: 250)
        id
        fields {
          slug
        }
        frontmatter {
          title
          description
        }
      }
    }
  }
`;
